import { Component, OnInit } from '@angular/core'; import { Router, ActivatedRoute } from '@angular/router'; import { Subscription } from 'rxjs/Subscription';  import { Evidencia_helpdesk } from '../../../modelo/Evidencia_helpdesk'; import { ObjetoVirtual } from '../../../modelo/ObjetoVirtual'; import { PesquisaService } from '../../../core/services/pesquisa/pesquisa.service';  @Component({   moduleId: module.id,   selector: 'evidencia_helpdesk-dados-edit',   templateUrl: 'evidencia_helpdesk_dadosEdit.component.html' }) export class Evidencia_helpdeskDadosEditComponent implements OnInit {    evidencia_helpdesk: Evidencia_helpdesk;    private sub: Subscription;    private errorMessage: string;     constructor(private router: Router,               private route: ActivatedRoute,               private pesquisaService: PesquisaService) { }    ngOnInit() {       this.sub = this.route.parent.params.subscribe(params => {         let o : ObjetoVirtual = new ObjetoVirtual();         o.classeJava = "br.com.primum.modelo.evidencia_helpdesk.Evidencia_helpdesk";                 let id = +params['id'];         this.evidencia_helpdesk = new Evidencia_helpdesk();         this.evidencia_helpdesk.cd_evidencia_helpdesk = id;         o.objetoJson = this.evidencia_helpdesk;          this.pesquisaService.efetuarPesquisa(o)             .subscribe(             result => this.onEfetuarPesquisa(result),             error =>  this.errorMessage = <any>error);       });   }    onEfetuarPesquisa(result : any) {     if (!result) {       this.errorMessage = 'Houve um erro ao pesquisa a evidencia_helpdesk. ';       }      this.evidencia_helpdesk = result;   }    ngOnDestroy() {     this.sub.unsubscribe();   }     onSubmit() {         }     onCancel(event: Event) {     event.preventDefault();     this.router.navigate(['/']);   }  }


